import { Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { MailTemplate } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { Box, MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";

export default function ManageTemplateMailModifyComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [code, setCode] = useState<string>('');
    const [title, setTitle] = useState<string>('');
    const [content, setContent] = useState<string>('');
    const keyData: string | undefined = useParams().key;

    const nav = useNavigate();
    const loadData = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            MailTemplate.GetDetail(keyData, (data: any) => {
                const row = data.data.data;
                setCode(row.code);
                setTitle(row.title);
                setContent(row.content);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [keyData]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleSubmitForm() {
        const body = {
            title: title,
            code: code,
            content: content
        };

        setIsLoading(true);
        if (keyData === undefined) {
            add(body);
        } else {
            edit(body);
        }
    }

    function add(body: any) {
        MailTemplate.Add(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    function edit(body: any) {
        MailTemplate.Detail(keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            MailTemplate.Edit(keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                setIsLoading(false);
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    function onClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa template này không?"
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                Delete();
            }
        })
    }

    function Delete() {
        MailTemplate.Detail(keyData, (data: any) => {
            MailTemplate.Delete(keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                nav("/admin/template");
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            isLoading={isLoading}
            title={keyData === undefined ? 'Thêm template' : 'Chỉnh sửa tenplate'} >
            <Box>
                <Form layout="vertical">
                    <MyFormItem.Input
                        label="Code"
                        value={code}
                        setValue={setCode} />
                    <MyFormItem.Input
                        label="Tiêu đề"
                        value={title}
                        setValue={setTitle} />
                    <MyFormItem.TextArea
                        label="Nội dung"
                        value={content}
                        setValue={setContent} />
                    <div dangerouslySetInnerHTML={{ __html: content }} className="preview">
                    </div>
                    <hr />
                    <MyButton
                        value={keyData === undefined ? 'Thêm' : 'Chỉnh sửa'}
                        onClick={handleSubmitForm}
                        type="success" />

                    <MyButton
                        type="danger"
                        value="Xóa"
                        onClick={onClickDelete} />
                </Form>
            </Box>
        </MyBox>
    );
}